<template>
  <nav>
    <ul>
      <li
        v-for="(section ,index) in sectionList"
        :key="index"
        v-bind:class="[{ itemSelect:(moveSelectIndex == index) }, section.className]"
        @mouseover="userSelectMenuItemAction(index)"
        @mouseout="userMoveOutMenuItemAction"
        @click="userClickOutMenuAction(index)"
      ></li>
    </ul>
  </nav>
</template>

<script>
export default {
  data() {
    return {
      moveSelectIndex: -1,
      sectionList: [
        {
          name: "mineTitle",
          className: "mine"
        },
        {
          name: "insetTitle",
          className: "inset"
        },
        {
          name: "appTitle",
          className: "app"
        },
        {
          name: "webTitle",
          className: "web"
        },
        {
          name: "otherTitle",
          className: "other"
        }
      ]
    };
  },
  methods: {
    userSelectMenuItemAction(index) {
      this.moveSelectIndex = index;
    },
    userMoveOutMenuItemAction() {
      this.moveSelectIndex = -1;
    },
    userClickOutMenuAction(index) {
      // 判断当前路由是否在详情界面
      if (this.isDetail) {
        this.$store.commit("home/changeIsDetailState", false);
        this.$router.push("/home");
      } else {
        var IdString = this.sectionList[index].name;
        console.log(IdString);
        if (index == 0) {
          document.documentElement.scrollTop = 0;
        } else {
          var offSet = document.getElementById(IdString).offsetTop;
          document.documentElement.scrollTop = offSet + 90;
        }
      }
    }
  },
  computed: {
    isDetail() {
      return this.$store.state.home.isDetail;
    }
  }
};
</script>

<style scoped>
nav {
  float: left;
  margin-left: 100px;
}
ul {
  position: absolute;
  bottom: 22.5px;
  list-style: none;
}
ul li {
  width: 120px;
  height: 30px;
  float: left;
  background-attachment: fixed;
  background: url("../assets/header_buttons.jpg") no-repeat;
}

.mine {
  background-position-x: 0px;
}
.inset {
  background-position-x: -120px;
}
.app {
  background-position-x: -240px;
}
.web {
  background-position-x: -360px;
}
.other {
  background-position-x: -480px;
}
.itemSelect {
  transition: background-position-y 0.3s;
  background-position-y: -30px;
}
</style>


