<template>
  <div id="detail">
    <transition appear enter-active-class="animated fadeInUp fast animationTiming">
      <DetailHeader v-bind:detailData="detailData" />
    </transition>
    <img
      class="detail_image"
      v-for="(imgURL,index) in detailData.imgList"
      :key="index"
      :src="require('../assets/'+ imgURL)"
    />
    <Footer />
  </div>
</template>

<script>
import DetailHeader from "./DetailHeader";
import Footer from "./Footer";
export default {
  name: "detail",
  data() {
    return {
      detailData: null,
    };
  },
  mounted() {
    var sectionIndex = this.$route.query.sectionIndex;
    var dataIndex = this.$route.query.dataIndex;
    var home = null;
    if (sessionStorage.getItem("userStore")) {
      var state = JSON.parse(sessionStorage.getItem("userStore"));
      home = state.home;
    } else {
      home = this.$store.state.home;
    }
    switch (sectionIndex) {
      case 0:
        this.detailData = home.insetList[dataIndex];
        break;
      case 1:
        this.detailData = home.appList[dataIndex];
        break;
      case 2:
        this.detailData = home.webList[dataIndex];
        break;
      case 3:
        this.detailData = home.otherList[dataIndex];
        break;
    }
  },
  components: { DetailHeader, Footer }
};
</script>

<style scoped>
#detail {
  background-color: #f2f2f2;
}
.animationTiming {
  animation-timing-function: ease-out;
}
.detail_image {
  width: 80%;
  margin-left: 10%;
  margin-bottom: 30px;
}
</style>


