<template>
  <div id="HomeDiv">
    <HomeHeader />
    <div class="titleDiv" id="insetTitle">
      <img src="../assets/home_title_inset.png" />
    </div>
    <div class="itemListDiv">
      <ListItem
        class="imageItem"
        v-for="(item,index) in insetList"
        :key="index"
        v-bind:itemData="item"
        v-bind:dataIndex="index"
      />
    </div>
    <div class="titleDiv" id="appTitle">
      <img src="../assets/home_title_app.png" />
    </div>
    <div class="itemListDiv">
      <ListItem
        class="imageItem"
        v-for="(item,index) in appList"
        :key="index"
        v-bind:itemData="item"
        v-bind:dataIndex="index"
      />
    </div>
    <div class="titleDiv" id="webTitle">
      <img src="../assets/home_title_web.png" />
    </div>
    <div class="itemListDiv">
      <ListItem
        class="imageItem"
        v-for="(item,index) in webList"
        :key="index"
        v-bind:itemData="item"
        v-bind:dataIndex="index"
      />
    </div>
    <div class="titleDiv" id="otherTitle">
      <img src="../assets/home_title_other.png" />
    </div>
    <div class="itemListDiv">
      <ListItem
        class="imageItem"
        v-for="(item,index) in otherList"
        :key="index"
        v-bind:itemData="item"
        v-bind:dataIndex="index"
      />
    </div>
    <Footer />
  </div>
</template>

<script>
import HomeHeader from './HomeHeader';
import Footer from "./Footer";
import ListItem from "./ListItem";

export default {
  name: "Home",
  methods: {},
  computed: {
    isShowUserDetail() {
      var isShow = this.$store.state.home.scorllOffY > 190 ? true : false;
      return isShow;
    },
    insetList() {
      return this.$store.state.home.insetList;
    },
    appList() {
      return this.$store.state.home.appList;
    },
    webList() {
      return this.$store.state.home.webList;
    },
    otherList() {
      return this.$store.state.home.otherList;
    }
  },
  components: {
    HomeHeader,
    ListItem,
    Footer
  }
};
</script>

<style scoped>
#HomeDiv {
  overflow-y: scroll;
  text-align: center;
}
.titleDiv {
  margin: 50px auto 0px auto;
  width: 754px;
  height: 129px;
}
.titleDiv > img {
  width: 100%;
}
.itemListDiv {
  margin: 50px auto;
  width: 1260px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.imageItem {
  width: 400px;
  height: 250px;
  margin: 10px;
}
</style>


