<template>
  <div class="home_header">
    <div class="home_header_name" id="home_header_name">Ling</div>
    <div v-if="isShow == 0" class="home_header_subtitle" id="home_header_subtitle">追求最完美的作品</div>
    <div
      appear
      v-show="isShow == 1"
      class="user_details_content"
      v-for="(content,index) in detailList"
      :key="index"
      v-bind:id="'user_details_content' + index"
    >{{content}}</div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isStartAnimation: 0,
      isShow: 0,
      detailList: [
        "26岁,工作三年时间,一直在追求最完美的作品",
        "给我一个灵感,我可以给你设计出最完美的世界",
        "希望不会辜负自己的青春,每一次机会都会好好把握",
        "爱生活,爱设计,更爱学习,在Design的路上不断前行",
        "不忘初心,方得始终"
      ]
    };
  },
  mounted() {
    window.addEventListener("scroll", this.handleScroll);
  },
  destroyed() {
    window.removeEventListener("scroll", this.handleScroll);
  },
  methods: {
    handleScroll() {
      var scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      var projectItemTop = document.getElementById("home_header_name")
        .offsetTop;
      if (scrollTop >= projectItemTop + 160 && this.isStartAnimation == 0) {
        this.isStartAnimation = 1;
        var subtitle = document.getElementById("home_header_subtitle");
        subtitle.classList.add("animated");
        subtitle.classList.add("fadeOutUp");
        var _this = this;
        subtitle.addEventListener("webkitAnimationEnd", function() {
          _this.isShow = 1;
          for (var index = 0; index < 6; index++) {
            var p = document.getElementById("user_details_content" + index);
            p.classList.add("animated");
            p.classList.add("fadeInUp");
            var animationDelay = "animation-delay:" + index * 0.1 + "s;";
            p.setAttribute("style", animationDelay);
          }
        });
      }
    }
  }
};
</script>

<style scoped>
.home_header {
  background: url("../assets/home_bg.jpg") rgba(0, 0, 0, 0.5) no-repeat;
  width: 100%;
  height: 1000px;
  background-blend-mode: multiply;
  overflow: hidden;
}
.home_header_name {
  padding-top: 300px;
  padding-bottom: 80px;
  text-align: center;
  color: #ffffff;
  font-size: 100px;
  font-weight: bolder;
}
.home_header_subtitle {
  text-align: center;
  color: #ffffff;
  font-size: 60px;
}
.user_details_content {
  padding-top: 40px;
  color: #ffffff;
  text-align: center;
  font-size: 24px;
  font: 20px/1.5 "微软雅黑", "黑体", "宋体", Simsun, "Arial Unicode MS", Mingliu,
    Helvetica;
}
</style>


