<template>
  <div class="header" v-bind:class="mainIsTop ? 'maxHeader' : 'minHeader'">
    <div id="logo">
      <div id="biggerDiv">
        <img id="biggerLogo" src="../assets/header_logo_bigger.jpeg" />
      </div>
      <div id="miniDiv">
        <img id="miniLogo" src="../assets/header_logo_mini.jpeg" />
      </div>
    </div>
    <HeaderMenu />
  </div>
</template>

<script>
import HeaderMenu from "./HeaderMenu";

export default {
  name: "Header",
  data() {
    return {
      activeIndex: "1"
    };
  },
  computed: {
    mainIsTop() {
      return this.$store.state.home.mainIsTop;
    }
  },
  methods: {},
  components: {
    HeaderMenu
  }
};
</script>

<style scoped>
.minHeader {
  height: 75px;
}
.maxHeader {
  height: 160px;
}
.header {
  width: 100%;
  overflow: hidden;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  min-width: 1400px;
  background-color: #fff;
  border-bottom: 1px solid #f3f3f3;
  -webkit-transition: height 0.5s;
  -moz-transition: height 0.3s;
  -ms-transition: height 0.3s;
  -o-transition: height 0.3s;
  transition: height 0.3s;
}
#logo {
  float: right;
  height: 100%;
}
#biggerDiv {
  position: absolute;
  right: 15%;
  top: 0;
  display: inline-block;
  line-height: 160px;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  -ms-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}
#miniDiv {
  position: absolute;
  right: 10%;
  top: 0;
  display: inline-block;
  line-height: 75px;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  -ms-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}

div.maxHeader #logo #biggerDiv {
  opacity: 1;
  -moz-transform: scale(1, 1);
  -webkit-transform: scale(1, 1);
  -ms-transform: scale(1, 1);
  -o-transform: scale(1, 1);
  transform: scale(1, 1);
}
div.minHeader #logo #biggerDiv {
  opacity: 0;
  -moz-transform: scale(0, 0);
  -webkit-transform: scale(0, 0);
  -ms-transform: scale(0, 0);
  -o-transform: scale(0, 0);
  transform: scale(0, 0);
}
div.maxHeader #logo #miniDiv {
  opacity: 0;
  -moz-transform: scale(0, 0);
  -webkit-transform: scale(0, 0);
  -ms-transform: scale(0, 0);
  -o-transform: scale(0, 0);
  transform: scale(0, 0);
}
div.minHeader #logo #miniDiv {
  opacity: 1;
  -moz-transform: scale(1, 1);
  -webkit-transform: scale(1, 1);
  -ms-transform: scale(1, 1);
  -o-transform: scale(1, 1);
  transform: scale(1, 1);
}
#biggerLogo {
  vertical-align: middle;
}
#miniLogo {
  vertical-align: middle;
}
</style>


