<template>
  <div class="detailHeader">
    <div class="shareBtn">
      <img src="../assets/detail_share_btn.png" />
    </div>
    <p class="category">
      {{detailData.sectionTitle}}
      <span>/</span>
    </p>
    <div class="title">{{detailData.title}}</div>
    <div class="time">{{detailData.time}}</div>
  </div>
</template>

<script>
export default {
  props: { detailData: Object }
};
</script>

<style scoped>
.detailHeader {
  padding: 50px 10%;
}
.category {
  position: relative;
  font-size: 14px;
  line-height: 14px;
  color: #333;
  white-space: nowrap;
}
.category span {
  display: inline-block;
  background: url("../assets/detail_category.png") no-repeat 0 3px;
  width: 8px;
  height: 14px;
  text-indent: -999px;
  margin-left: 15px;
  text-align: left;
  font-size: 14px;
  line-height: 14px;
}
.title {
  display: inline-block;
  margin-top: 25px;
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  font-size: 30px;
  color: #000;
  background: url("../assets/detail_title_bottom.png") repeat-x bottom;
}
.time {
  margin-top: 80px;
  font-size: 12px;
  height: 12px;
  line-height: 12px;
  color: #999;
}
.shareBtn {
  float: right;
  margin-right: 10%;
  text-align: center;
  position: relative;
}
.shareBtn img {
  position: absolute;
  top: 30px;
}
</style>


