<template>
  <figure
    class="home_list_item"
    @mouseover="userMoveInAction()"
    @mouseout="userMoveOutAction()"
    @click="userPushDetailAction()"
  >
    <img v-show="userSelect == 1" :src="selectImageURL" />
    <img v-show="userSelect == 0" :src="normalImageURL" />
    <figcaption v-show="userSelect == 1" class="projectTitle">{{itemData.title}}</figcaption>
  </figure>
</template>

<script>
export default {
  data() {
    return {
      userSelect: 0
    };
  },
  methods: {
    userMoveInAction() {
      this.userSelect = 1;
    },
    userMoveOutAction() {
      this.userSelect = 0;
    },
    userPushDetailAction() {
      this.$store.commit("home/changeIsDetailState", true);

      this.$router.push({
        name: "detail",
        query: {
          sectionIndex: this.itemData.sectionIndex,
          dataIndex: this.dataIndex
        }
      });
    }
  },
  computed: {
    normalImageURL() {
      return require("../assets/home_list_normal_" +
        this.itemData.titleImgIndex +
        ".png");
    },
    selectImageURL() {
      return require("../assets/" + this.itemData.titleImgIndex + ".png");
    }
  },
  props: { itemData: Object, dataIndex: Number }
};
</script>

<style scoped>
.home_list_item {
  border-radius: 5px;
  overflow: hidden;
}
.imageItem > img {
  width: 100%;
}
.projectTitle {
  position: relative;
  top: -65px;
  height: 60px;
  line-height: 60px;
  background-color: #f0f0f0;
  opacity: 0.8;
  padding-left: 10px;
}
</style>


