<template>
  <div id="app">
    <Header />
    <div ref="wrapper">
      <router-view id="mainView" />
    </div>
  </div>
</template>

<script>
import Header from "./components/Header";
import Vuex from "vuex";

export default {
  name: "App",
  created() {
    // 在页面加载时读取sessionStorage
    if (sessionStorage.getItem("userStore")) {
      this.$store.replaceState(
        Object.assign(
          {},
          this.$store.state,
          JSON.parse(sessionStorage.getItem("userStore"))
        )
      );
    }
    // 在页面刷新时将store保存到sessionStorage里
    window.addEventListener("beforeunload", () => {
      sessionStorage.setItem("userStore", JSON.stringify(this.$store.state));
    });
  },
  mounted() {
    window.addEventListener("scroll", this.handleScroll);
  },
  destroyed() {
    window.removeEventListener("scroll", this.handleScroll);
  },
  methods: {
    handleScroll() {
      var scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      this.$store.commit("home/changeScorllOffY", scrollTop);
      if (scrollTop >= 160) {
        this.$store.commit("home/changeMainIsTop", 0);
      } else {
        this.$store.commit("home/changeMainIsTop", 1);
      }
    }
  },
  components: {
    Header
  }
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  padding: 0;
  margin: 0;
  min-width: 1400px;
}
#mainView {
  position: absolute;
  top: 160px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  min-width: 1400px;
}
#maxMainView {
  position: absolute;
  top: 75px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  min-width: 1400px;
}
p {
  font: 12px/1.5 "微软雅黑", "黑体", "宋体", Simsun, "Arial Unicode MS", Mingliu,
    Helvetica;
}
</style>



